<template>
  <div class="game">
    <div class="leftCol">
      <h3>Команды</h3>
      <input type="text" v-model="newTeam.nickname" placeholder="Название" class="textbox_teams">
      <input type="text" v-model="newTeam.balance" placeholder="Баланс" class="textbox_teams"><br/>
      <input type="button" value="Создать команду" @click="teamAdder(newTeam)" class="game_button_add">
      <input type="button" value="Очистить команды" @click="clearTeams()" class="game_button_delete">
      <div v-for="team in teams" :key="team.nickname">
        <router-link to="/team1" class="team_link"><h4>{{team.nickname}}</h4></router-link>
        <div class="team">
        Баланс:{{team.balance}}<br/>
        Ресурсы:<br/>
        ResourceName: Count<br/>
        <input type="button" value="Присоединиться к команде" class="game_button_add">
        </div>
      </div>
    </div>
    <div class="midCol">
      <input type="button" value="Сохранить настройки" class="game_button_save" @click="saveTables()">
      <input type="button" value="Удалить настройки" class="game_button_delete" @click="clearMaterials()">
      <h1>Раунд 1</h1>
      <h3>Материалы</h3>
      <input type="button" value="Добавить материал" @click="tables.push('')" class="game_button_add"><br/>
      <div v-if="this.materials.length==0">
      <div  v-for="table in tables" :key="table">
      <p>Условные единицы:<input type="text" placeholder="Аршин"></p>
      <input type="button" value="Добавить строку" @click="rows.push('')" class="game_button_add"><br/>
      <table  border="1">
          <thead>
              <tr>
                  <td><input type="text"></td>
                  <td>Цена за У.е., руб</td>
                  <td>Мощность завода</td>
                  <td>Цена доставки 1м. на 1км. пути</td>
                  <td>Расстояние</td>
                  <td visibility="hidden">Расстояние 2</td>
                  <td></td>
              </tr>
          </thead>
          <tbody>
        <tr v-for="row in rows" :key="row">
          <td v-for="column in columns" :key="column"><input type="text"></td>
          <td><input type="button" value="Купить"></td>
        </tr>
        </tbody>
      </table>
      </div>
      </div>
      <div v-else>
      <div  v-for="(table, table_i) in materials" :key="table_i">
      <p>Условные единицы:<input type="text" placeholder="Аршин"></p>
      <input type="button" value="Добавить строку" @click="rows.push('')" class="game_button_add"><br/>
      <table  border="1">
          <thead>
              <tr>
                  <td><input type="text"></td>
                  <td>Цена за У.е., руб</td>
                  <td>Мощность завода</td>
                  <td>Цена доставки 1м. на 1км. пути</td>
                  <td>Расстояние</td>
                  <td>Расстояние 2</td>
                  <td></td>
              </tr>
          </thead>
          <tbody>
        <tr v-for="(row, row_i) in table" :key="row_i">
          <td v-for="(item, item_i) in row" :key="item_i">{{item}}</td>
          <td><input type="button" value="Купить"></td>
        </tr>
        </tbody>
      </table>
      </div>
      </div>
    </div>
    <div class="rightCol">
      <ul>
        <router-link to="/game" class="nav_src"><li><img src="@/assets/loc.png" alt="" class="navImg"><span class="tooltip_text">Города</span></li><br/></router-link>
        <router-link to="/materials" class="nav_src"><li><img src="@/assets/gear.png" alt="" class="navImg"><span class="tooltip_text">Материалы</span></li><br/></router-link>
      </ul>
    </div>
  </div>
</template>
<script>
import { mapActions } from 'vuex'
import { mapState } from 'vuex'
export default {
  computed:mapState({
    teams: state => state.team.teams,
    materials: state => state.settings.materials
  }),
    data(){
    return{
    newTeam:{nickname:'', balance:''},
    rows:[{}],
    columns:[{},{},{},{},{},{}],
    tables:[],
    //materials:[]
    }
  },
  methods:{
    ...mapActions('team',['addTeam']),
    ...mapActions('team',['clearTeams']),
    ...mapActions('settings',['addMaterial']),
    ...mapActions('settings',['clearMaterials']),
    teamAdder(team){
      this.addTeam(team)
      this.newTeam={}
    },
    saveTables(){
      var tables = document.getElementsByTagName('table')
      var row=[]
      var temp=[]
      var table=[]
      for(var k=0;k<tables.length;k++){
        table = tables[k]
        var rows = table.rows.length
        var columns = table.rows[0].cells.length
        for(var i=1;i<rows;i++){
          for(var j=0;j<columns-1;j++){
            row.push(table.rows[i].cells[j].children[0].value)
          }
          temp.push(row)
          row=[]       
      }
      this.addMaterial(temp)
      temp=[]
      }
    }
  }
}

</script>
<style>
table{
  margin: auto;
  width: auto;
}
.nav_src{
  position: relative;
  display: inline-block;
}
.nav_src .tooltip_text {
  visibility: hidden;
  width: 120px;
  background-color: black;
  color: #fff;
  text-align: center;
  padding: 5px 0;
  border-radius: 6px;
  position: absolute;
  z-index: 1;
  top: 5px;
  right: 80%;
}
.navImg:hover ~ .tooltip_text {
  visibility: visible;
}
.textbox_teams{
  width: 85px;
  margin-bottom: 5px;
}
.game_button_add{
  border-radius: 5px;
  text-decoration: none;
  display: inline-block;
  padding: 5px 10px;
  background-image: linear-gradient(45deg, #6ab1d7 0%, #33d9de 50%, #002878 100%);
  background-position: 100% 0;
  background-size: 200% 200%;
  color: white;
  box-shadow: 0 16px 32px 0 rgba(0, 40, 120, .35);
  transition: .5s;
  margin:2px;
}
.game_button_delete{
  border-radius: 5px;
  text-decoration: none;
  display: inline-block;
  padding: 5px 10px;
  background-image: linear-gradient(45deg, white 0%, red 50%, black 100%);
  background-position: 100% 0;
  background-size: 200% 200%;
  color: white;
  box-shadow: 0 16px 32px 0 rgba(0, 40, 120, .35);
  transition: .5s;
  margin:2px;
}
.game_button_save{
  border-radius: 5px;
  text-decoration: none;
  display: inline-block;
  padding: 5px 10px;
  background-image: linear-gradient(45deg, rgb(94, 243, 106) 0%, rgb(33, 219, 16) 50%, rgb(6, 114, 11) 100%);
  background-position: 100% 0;
  background-size: 200% 200%;
  color: white;
  box-shadow: 0 16px 32px 0 rgba(0, 40, 120, .35);
  transition: .5s;
  margin:2px;
}
.game_button_add:hover, .game_button_delete:hover, .game_button_save:hover{
  box-shadow: 0 0 0 0 rgba(0, 40, 120, 0);
  background-position: 0 0;
}
ul{
  text-align: left;
}
.game{
  display: flex;
  height: 90vh;
}
.leftCol{
  width: 20%;
  padding: 5px;
}
.rightCol{
  width: 5%;
}
.leftCol, .rightCol, .midCol{
  margin-top: 10px;
  padding: 5px;
}
.midCol{
  width: 75%;
  border-right: 1px;
  border-top: 0px;
  border-bottom: 0px;
  border-left: 1px;
  border-color: blue;
  border-style: solid;
  border-radius: 1px;
  padding: 5px;
}

.navImg{
  width: 50px;
  height: 50px;
}
.team{
  border-style: solid;
  border-radius: 5px;
  border-width: 3px;
}
</style>